.contact {
  background-color: rgb(12, 4, 20);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact h1 {
  margin-top: 80px;
  font-size: 40px;
  font-weight: lighter;
}

.pop-up-message {
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 32px 3px rgba(0, 0, 0, 0.8);
  position: fixed;
  max-width: 500px;
  background-color: rgb(49, 47, 47);
  z-index: 100;
  left: calc((50vw - 50%) * -1);
  top: calc((50vh - 50%) * -1);
  transform: translate(calc(50vw - 50%), calc(50vh - 50%));
}
.pop-up-message .close-message {
  top: 10px;
  right: 10px;
  position: absolute;
  cursor: pointer;
}
.pop-up-message p {
  margin-top: 20px;
  height: 35px;
}
@media screen and (max-width: 600px) {
  .pop-up-message {
    margin: 10px 0;
  }
}

.contact-container {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 6rem;
  gap: 5rem;
}
@media screen and (max-width: 1100px) {
  .contact-container {
    flex-direction: column;
    padding: 2rem;
    align-items: center;
  }
}

.contact-informations-container {
  width: 100%;
  max-width: 500px;
}

.contact-information {
  background-color: rgb(19, 18, 18);
  padding: 2rem;
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 2rem;
  border-radius: 8px;
}
.contact-information p {
  font-size: 1.2rem;
}
.contact-information p a {
  text-decoration: none;
  color: white;
}
.contact-information p a:hover {
  color: rgb(35, 35, 145);
}
@media screen and (max-width: 800px) {
  .contact-information p {
    font-size: 0.8rem;
  }
}

.icon {
  color: white;
  font-size: 40px;
  background-color: rgb(12, 4, 20);
  padding: 1.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.contact-form-container {
  width: 100%;
  max-width: 500px;
}

.form-grup {
  width: 100%;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
}
.form-grup label {
  font-size: 1.7rem;
  margin-bottom: 1rem;
}
.form-grup input {
  background-color: rgb(19, 18, 18);
  color: white;
  font-size: 1.4rem;
  padding: 1rem;
  outline: none;
  border: none;
  border-radius: 8px;
}
.form-grup textarea {
  background-color: rgb(19, 18, 18);
  color: white;
  font-size: 1.2rem;
  padding: 1rem;
  outline: none;
  border: none;
  border-radius: 10px;
}

.btn-submit {
  color: white;
  background-color: rgb(35, 35, 145);
  border: none;
  font-size: 20px;
  font-weight: bolder;
  padding: 15px 25px;
  border-radius: 8px;
  margin-bottom: 2rem;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
.btn-submit:hover {
  background-color: white;
  color: black;
}/*# sourceMappingURL=contact.css.map */